<template>
  <div id="container">
    <van-nav-bar title="单词PK" left-arrow :border="true" @click-left="back()"></van-nav-bar>
    <div id="main" v-if="!this.flag">
      <van-button id="btn" type="primary" block @click="matchPlayer()">
        匹配对手
      </van-button>
      <h2 v-if="this.showWaitTime&&this.waitTime">等待时间：{{this.waitTime}}s</h2>
      <van-button type="primary" block @click="cancelQueue()">取消排队</van-button>
    </div>
    <div v-if="this.flag">

      <div id="content">
        <header>
          <div class="headImg">
            <div class="box1">
              <img :src="userInfo.headImgUrl" />
              <div>{{userInfo.nickName}}</div>
            </div>
            <h2>{{curCount}}</h2>
            <div class="box2">
              <img :src="opponentUser.headImgUrl" />
              <div>{{opponentName}}</div>
            </div>
          </div>
        </header>
        <main>

          <div class="bar1">
            <div class="process">{{this.score}}</div>
          </div>

          <div class="qa">
            <div class="question">{{this.words[this.currentTimu].englishWord }}</div>
            <div class="option">
              <div :class="optionsStyle[0]" @click="answerEvent($event,0)">{{words[currentTimu].queone}}</div>
              <div :class="optionsStyle[1]" @click="answerEvent($event,1)">{{words[currentTimu].quetow}}</div>
              <div :class="optionsStyle[2]" @click="answerEvent($event,2)">{{words[currentTimu].quethree}}</div>
              <div :class="optionsStyle[3]" @click="answerEvent($event,3)">{{words[currentTimu].quefour}}</div>
            </div>
          </div>
          <div class="bar2">
            <div class="process">{{this.opScore}}</div>
          </div>
        </main>


      </div>

    </div>

    <van-dialog />

  </div>
</template>

<script>
  import { Dialog, Toast } from 'vant';

  export default {
    name: 'websocket',
    async created() {
      console.log('created')
      const { data: res } = await this.$http.get('/user/queryUserByOpenId')
      if (res) {
        this.userInfo = res
      }
    },
    data: function () {
      return {
        websocket: null,
        username: window.sessionStorage.getItem("token"),
        opponentName: '',
        waitTime: 0,
        showWaitTime: false,
        timer: null,
        flag: false,
        // 下面是答题部分变量
        words: [],
        curCount: 10,
        currentTimu: 0,
        optionsStyle: ['optionItem', 'optionItem', 'optionItem', 'optionItem'],
        isChoose: false,
        score: 0,
        opScore: 0,
        userInfo: {},
        opponentUser: {},
        countDownTimer: null,
        btn: null,
        show: true
      }
    },
    mounted() {
      this.btn = document.querySelector('#btn')
      this.btn.disabled = false
    },
    methods: {
      // 在方法里调用 this.websocketsend()发送数据给服务器
      onConfirm() {
        // 需要传输的数据
        const data = {
          code: 1,
          item: '传输数据'
        }
        this.websocketsend(JSON.stringify(data))
      },

      initWebSocket(name) { // 初始化weosocket
        this.websock = new WebSocket('ws://106.14.44.65:8889/websocket/' + name)
        this.websock.onmessage = this.websocketonmessage
        this.websock.onerror = this.websocketonerror
        this.websock.onopen = this.websocketonopen
        this.websock.onclose = this.websocketclose
      },
      websocketonopen() { // 连接建立之后执行send方法发送数据
        const data = {
          code: 0,
          msg: '这是client：初次连接'
        }
        this.websocketsend(JSON.stringify(data))
      },
      websocketonerror() {
        console.log('WebSocket连接失败')
      },
      //数据接收可能会有延迟
      websocketonmessage(e) { // 数据接收
        console.log('数据接收' + e.data)
        var obj = JSON.parse(e.data);
        let process2 = document.querySelector('.bar2 .process')

        // this.$store.commit('updatepkwords',obj.content.words)
        //如果等待队列里有人，那么匹配成功
        if (obj.message === "match") {
          //如果点击匹配，立即匹配成功，则清空定时器，不显示等待时间
          this.showWaitTime = false
          this.waitTime = 0
          clearInterval(this.timer)
          this.opponentUser = obj.content.opponentUser
          this.opponentName = obj.content.opponentUser.openId;
          // alert("匹配成功，你的对手是:" + this.opponentName);
          //匹配成功，显示答题内容
          this.flag = true
          console.log(obj.content.words)
          this.words = obj.content.words
          //开始倒计时
          this.countDownTimer = setInterval(this.countDown, 1000)
        } else if (obj.message == "score") {
          //只有当回答正确才会接收到值为score的message
          {
            this.opScore += 10;
            let sub = (1 - this.opScore / 10 / this.words.length) * 100;
            process2.style.transform = 'translateY(' + sub + '%)';
          }
        }
      },
      websocketsend(Data) { // 数据发送
        this.websock.send(Data)
      },
      websocketclose(e) { // 关闭
        console.log('已关闭连接', e)
      },
      //匹配对手
      async matchPlayer(e) {

        this.waitTime = 0
        this.showWaitTime = true
        this.initWebSocket(this.username);
        //定时器改变时间
        this.timer = setInterval(() => {
          this.waitTime++
        }, 1000)
        //加入等待队列
        await this.$http.get("http://106.14.44.65:8889/enterQueue/" + this.username).then((response) => {
          console.log("加入等待队列，返回状态为:" + response.status);
          console.log(response)
          if (response.status === 200) {
            this.btn.disabled = true
          } else {
            Toast({ message: '请重新排队' })
          }
        });
      },
      //提交答案,只有当前端验证所选为正确才会触发该方法
      async submitAnswer() {
        let param = {
          'username': this.username,
          'answer': true,
          'opponentName': this.opponentName
        };
        await this.$http.post("http://106.14.44.65:8889/submitAnswer", param).then((response) => {
          // console.log("返回状态为:" + response.status);
          console.log(response)
        })
      },
      //取消排队
      async cancelQueue() {
        //请求取消排队的接口
        await this.$http.get("http://106.14.44.65:8889/cancelQueue/" + this.username).then((response) => {
          if (response.data.code === '200') {
            console.log("取消排队，返回状态为:" + response.status);
            this.showWaitTime = false
            clearInterval(this.timer)
            this.btn.disabled = false
          } else {
            Toast({ message: '系统故障，请重试' })
          }
        });

      },
      countDown() {
        //每调用一次函数，计数减一
        this.curCount = this.curCount - 1
        //如果是最后一题，显示分数
        if (this.currentTimu === this.words.length) {
          clearInterval(this.countDownTimer)
          this.show = true
          Dialog.alert({
            title: '标题',
            message: `${this.score>this.opScore?this.userInfo.nickName:this.opponentName}赢了`,
            theme: 'round-button',
          }).then(async () => {
            await this.$router.push({ path: './setting' }).catch(data => {})
          });

        }

        //如果当前计数减到了0 之后，切换至下一题，并将计数恢复为10
        if (this.curCount === -1) {
          this.currentTimu++
          this.optionsStyle = ['optionItem', 'optionItem', 'optionItem', 'optionItem']
          this.isChoose = false
          this.curCount = 10
        }


      },
      answerEvent(e, index) {
        if (this.isChoose) {
          return
        }
        let currentWord = this.words[this.currentTimu]
        let optionsStyle = this.optionsStyle
        //获取当前题目的答案
        let currentAnswer = currentWord.realque
        //如果答案和点击的选项相同，那么设置该项样式为正确的样式
        if (currentAnswer === e.target.innerHTML) {
          let process1 = document.querySelector('.bar1 .process')

          optionsStyle[index] = 'correct'
          this.optionsStyle = optionsStyle
          this.isChoose = true
          this.score += 10
          let sub = 100 - this.score * 10 / this.words.length
          process1.style.transform = 'translateY(' + sub + '%)'
          this.submitAnswer()
        } else {
          //如果选择了错误的选项，显示错误的样式
          optionsStyle[index] = 'error'
          let itemIndex = 4
          // //如果那个选项是正确选项，那么样式为correct
          for (let key in currentWord) {
            //如果该项等于正确答案并且不是realque
            if (currentWord[key] === currentWord.realque && key !== 'realque') {
              //key就是正确的选项
              switch (key) {
              case 'queone':
                itemIndex = 0
                break
              case 'quetow':
                itemIndex = 1
                break
              case 'quethree':
                itemIndex = 2
                break
              case 'quefour':
                itemIndex = 3
                break
              default:
                break
              }
              optionsStyle[itemIndex] = 'correct'
            }
          }
          this.optionsStyle = optionsStyle

        }

      }

    },


    destroyed() {
      this.websock.close() // 离开路由之后断开websocket连接
      clearInterval(this.countDownTimer)
    },


  }
</script>

<style scoped>
  #container {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
  }

  #main {
    position: absolute;
    width: 100%;
    height: 100%-100px;
    top: 46px;
    bottom: 50px;
    background: url('../assets/bg.jpg');
    background-size: 100% 100%;
    opacity: 0.5;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }

  h2 {
    color: rgb(249, 253, 2);
  }

  .van-nav-bar {
    /*background-color: #66afb7;*/
    background: linear-gradient(to top, #98eebc, 25%, #66afb7);
    opacity: 1;
  }

  .van-button {
    width: 80vw;
    margin: 30px auto;
    opacity: 1;
    font-size: 20px;
    border-radius: 10px;

  }

  /* * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow: hidden;
    bottom: 50px;
  } */
  /* 下面是答题部分样式 */
  #content {
    position: absolute;
    background: linear-gradient(to right, #f7c6c6 50%, #c5f5f8 50%);
    width: 100vw;
    height: 100vh-100px;
    box-sizing: border-box;
    bottom: 50px;
    /* overflow: hidden; */
    top: 46px;
  }

  #content header {
    top: 30px;
    height: 20%;
    /* border: 1px solid red; */
    position: relative;
  }


  #content header .headImg {
    height: 80px;
    /* background-color: blanchedalmond; */
    display: flex;
    justify-content: space-between;
  }

  .headImg h2 {
    height: 80px;
    line-height: 80px;
    color: rgb(253, 113, 113);
    font-size: 30px;
    font-weight: bolder;
  }

  .box1 {
    position: relative;
    height: 80px;
    width: 100px;
    background-color: #c5f5f8;
    border-radius: 0 50% 50% 0;
  }

  .box2 {
    position: relative;
    height: 80px;
    width: 100px;
    background-color: #f7c6c6;
    border-radius: 50% 0 0 50%;
  }

  .box1 img {
    position: absolute;
    width: 65px;
    border-radius: 50%;
    top: 50%;
    left: 55%;
    transform: translate(-50%, -50%);
  }

  .box2 img {
    position: absolute;
    width: 65px;
    border-radius: 50%;
    top: 50%;
    right: 55%;
    transform: translate(50%, -50%);
  }

  #content main {
    height: 75%;
    /* border: 1px solid black; */
    position: absolute;
    width: 100%;
  }

  main .bar1 {
    width: 20px;
    height: 90%;
    background-color: cornsilk;
    border-radius: 10px;
    position: absolute;
    left: 10px;
    bottom: 20px;
    overflow: hidden;
  }

  main .bar2 {
    width: 20px;
    height: 90%;
    background-color: cornsilk;
    border-radius: 10px;
    position: absolute;
    right: 10px;
    bottom: 20px;
    overflow: hidden;
  }

  .process {
    width: 20px;
    height: 100%;
    border-radius: 10px;
    background-color: darkturquoise;
    transform: translateY(100%);
  }

  main .qa {
    height: 90%;
    /* width: 100%; */
    position: absolute;
    left: 40px;
    right: 40px;
    /* background-color: rgb(247, 238, 238); */
  }

  main .qa .question {
    height: 70px;
    line-height: 120px;
    text-align: center;
    font-size: 25px;
    font-weight: 500;
    color: firebrick;
  }

  .optionItem {
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: rgb(37, 213, 216);
    border-radius: 25px;
    margin: 30px 10px;
    color: white;
    bottom: 20px;
  }

  .correct {
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: rgb(37, 216, 132);
    border-radius: 25px;
    margin: 30px 10px;
    color: white;
    bottom: 20px;
  }

  .error {
    height: 50px;
    line-height: 50px;
    text-align: center;
    background-color: rgb(216, 88, 37);
    border-radius: 25px;
    margin: 30px 10px;
    color: white;
    bottom: 20px;
  }
</style>